define([

], function() {

	'use strict';

	return function mask(callback) {

		var canvas = document.createElement('canvas');
		var context = canvas.getContext('2d');
		var container = document.querySelector('#mask .image');
		var artwork = [].slice.call(document.querySelectorAll('.mask'));
		var dy = 0;

		/* Uncomment do see the canvas to debug */
		canvas.setAttribute('id', 'canvas');

		if (!container) return;

		// container.appendChild(canvas);

		var maskLoaded = function(art, mask, width, height) {

			canvas.width = width;
			canvas.height = height;

			/* Draw the plain black mask to the canvas */
			context.drawImage(mask, 0, 0, width, height);

			/* Masks the image on top of the crop, works like Photoshop bled modes */
			context.globalCompositeOperation = 'source-atop';

			/* Add image to the canvas mask */
			context.drawImage(art, 0, dy);

			/* Copy the canvas image to an HTML image tag */
			art.src = canvas.toDataURL();

			callback();
		};

		var croppedImageLoaded = function(art, croppedImage) {

			/* Native size of the mask */
			var width = croppedImage.width;
			var height = croppedImage.height;

			var mask = document.createElement('img');
				mask.src = art.getAttribute('data-mask');

			mask.onload = maskLoaded.bind(null, art, mask, width, height);

		};

		artwork.forEach(function(art) {

			var croppedImage = document.createElement('img');

			croppedImage.src = art.src;

			croppedImage.onload = croppedImageLoaded.bind(null, art, croppedImage);

		});

	};
});
