/* global	define,
			Power4,
			Elastic,
			Power0,
			RoughEase,
			Back */
define([

	'ScrollMagic',
	'TweenMax',
	'TimelineMax',
	'ScrollMagicAnimation',
	'ScrambleTextPlugin',
	'ScrollMagicAddIndicators'

], function(
	ScrollMagic,
	TweenMax,
	TimelineMax
	) {

	'use strict';

	var ease = {
		power: 		Power4.easeOut,
		elastic: 	Elastic.easeOut.config(1, 1),
		none: 		Power0.easeNone,
		back: 		Back.easeOut.config(1),
		rough: 		RoughEase.ease.config({
						points: 100,
						strength: 4,
						clamp: true,
						randomize: true
					})
	};

	/* Shortcut to select all elements */
	var selectAll = function(element) {
		return [].slice.call(document.querySelectorAll(element));
	};

	/*
		Scramble Text and Fade In Animations for the Stats
		--------------------------------------------------
	*/

	/* Scramble function to pass to each instance with a [data-scram] attr */
	var scrambleText = function(element, prevText, i) {
		return new TimelineMax()
			.to(element, 1, {
				scrambleText: {
					text: prevText,
					revealDelay: 0,
					tweenLength: true,
					ease: ease.none
				},
				delay: i * 0.05,
				visibility: 'visible'
			});
	};

	/* Scramble function iterator */
	var scramble = function(attr, text, i) {
		var replace = text.getAttribute(attr);
		scrambleText(text, replace, i);
	};

	var scrambleReset = function(element) {
		element.style.visibility = 'hidden';
	};

	/* Main controller using the body element for scroll */
	var bodyController = new ScrollMagic.Controller();

	/* Wrap scenes/sections in an object just to organize */
	var scenes = {

		stats: function() {

			/* Wrap stats animations in an object */
			var _stats = {};


			/* Audience Data */
			_stats.audience = function() {

				var audience_box = selectAll('.stat');
				var audience_statsText = selectAll('.stat-audience [data-txt]');
				var audience_statsScram = selectAll('.stat-audience [data-scram]');

				/* Create timeline for audience stats */
				var audienceTL = new TimelineMax()
					.staggerFromTo(audience_box, 1, {
						y: '105%',
						ease: ease.elastic
					}, {
						y: '0%',
						ease: ease.elastic
					}, 0.1, 0)
					.staggerFromTo(audience_statsText, 0.6, {
						opacity: 0,
						scale: 0.9,
						ease: ease.power
					}, {
						opacity: 1,
						scale: 1,
						ease: ease.power
					}, 0.1, 0.2)
					.pause();

				var audienceScene = new ScrollMagic.Scene({
					triggerElement: audience_box,
					duration: '200%',
					triggerHook: 0.8,
					offset: -audience_box[0].clientHeight
				});

				audienceScene.on('enter leave', function(e) {
					if (e.type === 'enter') {
						audience_statsScram.forEach(scramble.bind(null, 'data-scram'));
						audienceTL.play();
					}
					else {
						audienceTL.reverse();
					}
				});

				audienceScene.addTo(bodyController);
			};

			/* Audience Index Data */
			_stats.audienceIndex = function() {

				var audienceIndex = selectAll('.stat-audience-index');
				var audienceIndex_bg = selectAll('.stat-audience-index .bg');
				var audienceIndex_values = selectAll('.stat-audience-index .range .value');
				var audienceIndex_statsText = selectAll('.stat-audience-index [data-txt]');
				var audienceIndex_statsScram = selectAll('.stat-audience-index [data-scram]');

				var audienceIndexTL = new TimelineMax()
					.staggerFrom(audienceIndex_bg, 1, {
						width: '0%',
						scaleX: 0,
						ease: ease.power
					}, 0.1, 0)
					.staggerFrom(audienceIndex_values, 1, {
						width: '0%',
						scaleX: 0,
						ease: ease.power
					}, 0.1, 0.3)
					.staggerFromTo(audienceIndex_statsText, 0.6, {
						opacity: 0,
						scale: 0.9,
						ease: ease.power
					}, {
						opacity: 1,
						scale: 1,
						ease: ease.power
					}, 0.1, 0.2)
					.pause();

				var audienceIndexScene = new ScrollMagic.Scene({
					triggerElement: audienceIndex,
					duration: '200%',
					triggerHook: 0.8
				})
				.on('enter leave', function(e) {
					if (e.type === 'enter') {
						audienceIndexTL.play();
						audienceIndex_statsScram.forEach(scramble.bind(null, 'data-scram'));
					}
					else {
						audienceIndexTL.reverse();
					}
				});

				audienceIndexScene
					.setClassToggle('.stat-audience-index .range', 'active')
					.addTo(bodyController);
			};

			/* Engagement Data */
			_stats.engagement = function() {

				var engagement_icons = selectAll('.stat-engagement .icon');
				var engagement_statsText = selectAll('.stat-engagement [data-txt]');
				var engagement_statsScram = selectAll('.stat-engagement [data-scram]');

				var enagementTL = new TimelineMax()
					.staggerFrom(engagement_icons, 1, {
						opacity: 0,
						ease: ease.power
					}, 0.45, 0)
					.staggerFromTo(engagement_statsText, 0.6, {
						opacity: 0,
						scale: 0.9,
						ease: ease.power
					}, {
						opacity: 1,
						scale: 1,
						ease: ease.power
					}, 0.1, 0.2)
					.pause();

				var engagementScene = new ScrollMagic.Scene({
					triggerElement: '.stat-engagement',
					duration: '200%',
					triggerHook: 0.8
				})
				.on('enter leave', function(e) {
					if (e.type === 'enter') {
						enagementTL.play();
						engagement_statsScram.forEach(scramble.bind(null, 'data-scram'));
					}
					else {
						enagementTL.reverse();
					}
				});

				engagementScene.addTo(bodyController);
			};

			/* Social Data */
			_stats.social = function() {

				var social_icons = selectAll('.stat-social-presence .icon');
				var social_box = selectAll('.stat-social-presence .data');
				var social_statsText = selectAll('.stat-social-presence [data-txt]');
				var social_statsScram = selectAll('.stat-social-presence [data-scram]');

				var socialTL = new TimelineMax()
					.staggerFrom(social_icons, 1, {
						opacity: 0,
						ease: ease.power
					}, 0.15, 0)
					.staggerFrom(social_box, 1, {
						opacity: 0,
						y: '100%',
						ease: ease.power
					}, 0.15, 0.15)
					.staggerFromTo(social_statsText, 0.6, {
						opacity: 0,
						scale: 0.9,
						ease: ease.power
					}, {
						opacity: 1,
						scale: 1,
						ease: ease.power
					}, 0.05, 0.3)
					.pause();

				var socialScene = new ScrollMagic.Scene({
					triggerElement: '.stat-social-presence',
					duration: '200%',
					triggerHook: 0.8
				})
				.on('enter leave', function(e) {
					if (e.type == 'enter') {
						socialTL.play();
						socialTL.addCallback(function() {
							social_statsScram.forEach(scramble.bind(null, 'data-scram'));
						}, 1);
					} else {
						socialTL.reverse();
						social_statsScram.forEach(scrambleReset);
					}
				});

				socialScene.addTo(bodyController);
			};

			/* Call all of stats animations */
			for ( var fn in _stats ) {
				if (typeof _stats[fn] === 'function' ) {
					_stats[fn]();
				}
			}
		},

		backgroundLogo: function() {

			var sections = selectAll('#media-kit > section');

			sections.forEach(function(sec) {
				var backgroundLogoScene = new ScrollMagic.Scene({
					triggerElement: sec,
					duration: sec.clientHeight,
					triggerHook: 1
				});

				backgroundLogoScene
					.setClassToggle('#media-kit', sec.className)
					.addTo(bodyController);
			});

		},

		brandIntegrations: function() {

			/* Main container */
			selectAll('.case').forEach(function(val) {

				var hero = val.querySelector('.case-hero');
				var video = val.querySelectorAll('.case-video');
				var bg = val.querySelectorAll('.case-bg');
				var image = val.querySelectorAll('.case-bg figure');
				var cover = val.querySelectorAll('.case-cover');
				var txt = val.querySelector('.case-txt');

				var heroTL = new TimelineMax()

					//- Case Study Video
					.fromTo(hero, 2, {
						width: '100%',
						height: '100%',
						ease: ease.none
					}, {
						width: '70%',
						height: '0%',
						ease: ease.none
					}, 0.5)
					.fromTo(video, 4, {
						opacity: 1
					}, {
						opacity: 0,
						ease: ease.none
					}, 0)

					//-BG Image & container
					.fromTo(bg, 3, {
						x: '-200%',
						ease: ease.none
					}, {
						x: '-35%',
						ease: ease.none
					}, 0)
					.fromTo(image, 3, {
						x: '-5%',
						ease: ease.none
					}, {
						x: '30%',
						ease: ease.none
					}, 0)

					//- Cover for text
					.fromTo(cover, 3, {
						x: '150%',
						ease: ease.none
					}, {
						x: '30%',
						ease: ease.none
					}, 2)
					.fromTo(txt, 2, {
						x: '150%',
						ease: ease.none
					}, {
						x: '0%',
						ease: ease.none
					}, 2);

				var caseScene = new ScrollMagic.Scene({
					triggerElement: val,
					duration: '300%',
					triggerHook: 0
				});

				caseScene
					.setPin(val)
					.setTween(heroTL)
					.tweenChanges(false)
					.addTo(bodyController);
			});
		},
		video: function() {

			selectAll('video').forEach(function(val) {

				/* Pause the videos when this initializes */
				val.pause();

				var playPauseScene = new ScrollMagic.Scene({
					triggerElement: val,
					duration: '100%',
					triggerHook: 0.5
				});

				playPauseScene.on('progress', function(e) {

					var inView =
						e.state === 'DURING' && e.scrollDirection === 'FORWARD' ||
						e.state === 'DURING' && e.scrollDirection === 'REVERSE';

					var play = function() {
						if (val.paused) {
							val.play();
						}
					};

					if (inView) {
						play();
					}
					else {
						val.pause();
					}
				});

				playPauseScene.addTo(bodyController);
			});
		},

		text: function() {
			selectAll('[data-text]').forEach(function(val) {
				return new ScrollMagic.Scene({
					triggerElement: val,
					duration: '200%',
					triggerHook: 1
				})
				.setClassToggle(val, 'active')
				.addTo(bodyController);
			});
		},

		logoWall: function() {

			var logos = selectAll('.brands-logo');

			logos.forEach(function(val) {

				/* Set the translateY and fill values */
				TweenMax.set(val, { y: '100%', fill: '#7D8080' });

				var brandLogosTL = new TimelineMax();

				/* Logo wall animation sequence */
				brandLogosTL
					.staggerFromTo(logos, 1, {
						y: '100%'
					}, {
						y: '0%',
						ease: ease.elastic
					}, 0.025)
					.pause();

				/* Add event for logo scene */
				return new ScrollMagic.Scene({
						triggerElement: '.brands',
						duration: '200%',
						triggerHook: 0.5
					})
					.on('enter leave', function(e) {

						/* Play timeline on enter or leave */
						return e.type === 'enter' ? brandLogosTL.play() : brandLogosTL.reverse();
					})
					.addTo(bodyController);
			});
		},
		bleedAndBreedBoxes: function() {

			var boxes = selectAll('.bleed-breed .box');

			var showArtFaces = function() {
				boxes.forEach(function(box, i) {
					var delay = i * 50;
					setTimeout(function() {
						box.classList.toggle('show-art-faces');
					}, delay);
				});
			};

			boxes.forEach(function(box) {

				var boxesSetUp = function() {
					box.classList.add('no-hover');
					TweenMax.set(box, { opacity: 0 });
				};

				boxesSetUp();

				var boxesComplete = function() {
					boxes.forEach(function(box) {
						box.classList.remove('no-hover');
					});
				};

				var boxesAppear = new TimelineMax()
					.staggerFromTo(boxes, 1, {
						opacity: 0,
						y: '-15%'
					}, {
						opacity: 1,
						y: '0%',
						ease: ease.elastic
					}, 0.10, 0, boxesComplete)
					.pause();

				var boxesScene = new ScrollMagic.Scene({
					triggerElement: '.bleed-breed',
					duration: '300%',
					triggerHook: 0.5
				});

				boxesScene
					.on('enter leave', function(e) {
						return e.type === 'enter' ? boxesAppear.play() : boxesAppear.reverse();
					})
					.addTo(bodyController);

				box.addEventListener('click', showArtFaces, false);
			});
		},
		images: function() {

			if (window.outerWidth > 767) {

				var imagesToScroll = selectAll('.intro figure');
				var images = selectAll('.intro figure img');

				imagesToScroll.forEach(function(val, i) {

					var deviationTL = new TimelineMax()
						.fromTo(val, 2, {
							y: i === 0 ? '50%' : '100%'
						}, {
							y: '-30%',
							ease: Power0.easeNone
						}, 0)
						.fromTo(images, 2, {
							scale: 1
						}, {
							scale: 1.75,
							ease: Power0.easeNone
						}, 0);

					var deviationScrollScene = new ScrollMagic.Scene({
						triggerElement: val,
						duration: i === 0 ? '200%' : '250%',
						triggerHook: 1,
						offset: -val.clientHeight / 2
					});

					deviationScrollScene
						.setTween(deviationTL)
						.tweenChanges(true)
						.addTo(bodyController);
				});

				var imagesToClip = selectAll('figure[data-clip]');

				imagesToClip.forEach(function(val) {
					var deviationClipScene = new ScrollMagic.Scene({
						triggerElement: val,
						duration: '200%',
						triggerHook: 1
					});
					deviationClipScene
						.on('enter leave', function() {
							val.classList.toggle('active');
						})
						.addTo(bodyController);
				});

			}
		}
	};

	//-----------------------------------------------------------

	function init() {
		for (var s in scenes) {
			if (typeof scenes[s] === 'function') {
				scenes[s]();
			}
		}
	}

	return {
		init: init
	};
});